<template>
    <!---->
    <div id="XSDFXPage" class="XSDFXPage">111</div>
</template>
<script>
    export default {
        name:'',
        data () {
            return {

            }
        },
        mounted() {
            // API
            // Map
            var map = new BMap.Map("XSDFXPage",{enableMapClick:true});
            // ,
            map.centerAndZoom(new BMap.Point(121.160724,31.173277), 11);
            // 
            map.addControl(new BMap.MapTypeControl());
            //  
            map.setCurrentCity("");
            // 
            map.enableScrollWheelZoom(true);
            // 
            /*setTimeout(function(){
                map.panTo(new BMap.Point(113.262232,23.154345));
            }, 2000);
            setTimeout(function(){
                map.setZoom(14);
            },4000);*/
            /************************************************
             
             *************************************************/
           // var pointGZ = new BMap.Point(113.262232,23.154345);
           // var pointHK = new BMap.Point(110.35,20.02);
           /* setTimeout(function(){
                var polyline = new BMap.Polyline([pointGZ,pointHK],{strokeColor:"blue",strokeWeight:5,strokeOpacity:0.5});
                map.addOverlay(polyline);
            },6000);*/
            /************************************************
             
             *************************************************/
            map.addControl(new BMap.ScaleControl({anchor:BMAP_ANCHOR_TOP_LEFT}));
            map.addControl(new BMap.NavigationControl());
            map.addControl(new BMap.NavigationControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,type:BMAP_NAVIGATION_CONTROL_SMALL}));
            /************************************************
             ZoomControl
             *************************************************/
            function ZoomControl() {
                //
                this.defaultAnchor = BMAP_ANCHOR_BOTTOM_RIGHT;
                this.defaultOffset = new BMap.Size(50,23);
            }
            //JavaScriptprototypeBMap.Control
            ZoomControl.prototype = new BMap.Control();
            //initializeDOM
            //div
            ZoomControl.prototype.initialize = function(map) {
                //DOM
                var div = document.createElement('div');
                //
                div.appendChild(document.createTextNode(''));
                //
                div.style.color = '#40C5CC';
                div.style.cursor = 'pointer';
                div.style.border = '3px solid gray';
                div.style.backgroundColor = '#eee';
                //
                div.onclick = function(e) {
                    map.setZoom(map.getZoom() + 2);
                }
                //DOM
                map.getContainer().appendChild(div);
                //DOM
                return div;
            }
            //
            var myZoomCtrl = new ZoomControl();
            map.addControl(myZoomCtrl)

            /************************************************
             
             *************************************************/
            map.addControl(new BMap.CityListControl({
                anchor:BMAP_ANCHOR_BOTTOM_RIGHT,
                offset:new BMap.Size(130,23)
                // 
                // onChangeBefore: function(){
                //    alert('before');
                // },
                // 
                // onChangeAfter:function(){
                //   alert('after');
                // }
            }));
            /************************************************
             
             *************************************************/
                //
           // var myIcon = new BMap.Icon("http://developer.baidu.com/map/jsdemo/img/fox.gif",new BMap.Size(300,157));
            //
           // var marker = new BMap.Marker(pointHK,{icon:myIcon});
            //var marker1 = new BMap.Marker(pointGZ,{icon:myIcon});
            //
            map.addOverlay(marker);
            map.addOverlay(marker1);
            //
            //var label = new BMap.Label('',{offset:new BMap.Size(140,10)});
            marker1.setLabel(label);
            //
            /*marker1.addEventListener('click',function(){
                var p = marker1.getPosition();//
                alert("" + p.lng + ',' + p.lat);
            })*/

            /************************************************
             
             *************************************************/
            var beijingPosition=new BMap.Point(116.432045,39.910683),
                hangzhouPosition=new BMap.Point(120.129721,30.314429),
                taiwanPosition=new BMap.Point(121.491121,25.127053);
            var point = [beijingPosition,hangzhouPosition,taiwanPosition];

            var curve = new BMapLib.CurveLine(point, {strokeColor:"blue", strokeWeight:3, strokeOpacity:0.5});//
            map.addOverlay(curve);//
            curve.enableEditing();//

            /************************************************
             
             *************************************************/
            var menu = new BMap.ContextMenu();

            var txtMenuItem = [
                {
                    text:'',
                    callback:function(){map.zoomIn()}
                },
                {
                    text:'',
                    callback:function(){map.zoomOut()}
                }
            ];
            for(var i=0; i < txtMenuItem.length; i++){
                menu.addItem(new BMap.MenuItem(txtMenuItem[i].text,txtMenuItem[i].callback,100));
            }
            map.addContextMenu(menu);
        }
    }
</script>
<style scoped>
    html,body,.XSDFXPage{
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin: 0px auto;
        font-family: "";
    }
</style>



